* {
    background: grey;
    color: white;
    margin: 0px;
    font-family: 'Courier New', Courier, monospace;
}

#arrowLeftBreak, #arrowLeftSession {
    display: flex;
    text-align: left;
    font-size: 40px;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none;
    -webkit-tap-highlight-color: transparent;
    cursor: pointer;
}

#arrowRightBreak, #arrowRightSession {
    display: flex;
    justify-content: flex-end;
    text-align: right;
    font-size: 40px;
    line-height: 0px;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none;
    -webkit-tap-highlight-color: transparent;
    cursor: pointer;
}

#arrowLeftBreak {
    padding-left: 8px;
}

#arrowLeftSession {
    padding-left: 38px;
}

#arrowRightBreak:active, #arrowLeftBreak:active, #arrowRightSession:active, #arrowLeftSession:active {
    color: black;
}

#toggle:active, #reset:active {
    color: black;
    -webkit-tap-highlight-color: transparent;
}

#toggle, #reset {
    width: 90px;
    text-align: center;
    cursor: pointer;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none;
    -webkit-tap-highlight-color: transparent;
}

#reset {
    margin-right: 5px;
}